@font-face {
  font-family: 'VC Henrietta';
  src: url('/assets/fonts/VCHenriettaCondensed-Medium.eot');
  src: url('/assets/fonts/VCHenriettaCondensed-Medium.eot?#iefix') format('embedded-opentype'),
      url('/assets/fonts/VCHenriettaCondensed-Medium.woff2') format('woff2'),
      url('/assets/fonts/VCHenriettaCondensed-Medium.woff') format('woff'),
      url('/assets/fonts/VCHenriettaCondensed-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  // font-display: swap;
}

